import React from "react";
import PropTypes from 'prop-types';

const PageElement = (props) => {
  // TODO: click time limit
  return <div>
    <button onClick={() => props.previousPage()} disabled={props.currPage === 1}>{"<"}</button>
    {props.currPage}
    <button onClick={() => props.nextPage()} disabled={props.currPage === props.totalPages}>{">"}</button>
  </div>;
};

PageElement.propTypes = {
  previousPage: PropTypes.func.isRequired,
  nextPage: PropTypes.func.isRequired,
  currPage: PropTypes.number.isRequired,
  totalPages: PropTypes.number.isRequired
};

export default PageElement;
