import React from 'react';
import '../App.css';
import Table from "./Table";
import * as actionCreators from "../actions";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import PageElement from "../components/PageElement";
import Loading from "../components/Loading";

class App extends React.Component {
  // componentWillMount() {
  //   const {shows} = this.props;
  //   if (shows.infoNeedToBeChanged) {
  //     this.props.fetchShows(shows.pageNumber, shows.itemsPerPage);
  //   }
  // }

  render() {
    const {shows} = this.props;
    const infoLoaded = shows.pageCount && shows.itemCount;

    if (shows.infoNeedToBeChanged) {
      this.props.fetchShows(shows.pageNumber, shows.itemsPerPage);
      return <div className="App">
        <Loading/>
      </div>
    }
    if (!infoLoaded) {
      return <div className="App">
        <Table/>
      </div>;
    }
    const extraInfo = <div>
      <p>TV-shows displayed: {shows.itemsPerPage}. Page {shows.pageNumber} of {shows.pageCount}</p>
      <p>Shows in total: {shows.itemCount}</p>
    </div>;
    return <div className="App">
      <PageElement previousPage={() => this.props.setPreviousPage()} nextPage={() => this.props.setNextPage()}
                   currPage={shows.pageNumber} totalPages={shows.pageCount}/>
      <Table/>
      {extraInfo}
    </div>

  }
}

const mapStateToProps = (state) => {
  return {
    shows: state.shows
  }
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(actionCreators, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
